@import url(https://fonts.googleapis.com/css?family=Roboto:400);
@import "libs";
@import "reset";
@import "vars";

body {
    font-family: $roboto;
    font-size: 14px;
    background-color: #333333;
}

.wrapper {
    display: flex;
    flex-direction: column;
    max-width: 1200px;
    padding: 0 2vh;
    margin: 0 auto;
}
/*_____HEADER____*/
.topheader {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  min-height: 80px;
}

.topmenu {
  display: flex;

  ul {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;

    li {

      a {
        font-size: 14px;
        color: $color_white;
        text-transform: uppercase;
        &:hover {
            color: $crocus;
        }
      }
     }
  }
}

.button {
  font-family: $roboto;
  text-align: center;
  padding: 10px 10px;
}

.topmenu-item {
  position: relative;
  transition: all 600ms ease-in;
}
.topmenu-item a {
  line-height: 25px;
  filter: progid:DXImageTransform.Microsoft.Alpha(enabled=false);
  opacity: 1;
  transition: all 300ms ease-in;
}
.topmenu-item a.out {
  filter: progid:DXImageTransform.Microsoft.Alpha(Opacity=0);
  opacity: 0;
}
.bt-sl-solid-l {
  position: absolute;
  left: 0;
  height: 100%;
  border-left: 2px solid orange;
  z-index: 1001;
  transition: all 500ms ease-in-out;
}
.bt-sl-solid-l.out {
  left: 50%;
}
.bt-sl-solid-l.down {
  height: 0%;
}

.bt-sl-solid-r {
  position: absolute;
  right: -2px;
  height: 100%;
  border-left: 2px solid orange;
  z-index: 1001;
  transition: all 500ms ease-in-out;
}
.bt-sl-solid-r.out {
  right: 50%;
}
.bt-sl-solid-r.down {
  height: 0%;
}


.social {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;

    // background-color: #555555;
}

.social .tel {
    font-family: inherit;
    width: auto;
    color: white;
    font-size: 16px;
    font-weight: 400;
    transition-duration: 0.3s;

    &:hover {
        transform: rotate(0deg);
        color: $crocus;
    }
}

.icon {
    font-size: 14px;
    fill: currentColor;
    color: #fff;
}

.social a {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    margin-right: 10px;
    transition: all 0.7s ease-in-out;
    font-family: "icomoon";
}

.social a:last-child {
    margin-right: 5px;
}

.social a:hover {
    transform: rotate(360deg);
}

.social-fb {
    background: rgba(59,89,152, 0.2);
}

.social-fb:hover {
    background: rgba(59,89,152, 0.5);
}

.social-instagam {
    background: rgba(208, 71, 209, 0.2);
}

.social-instagam:hover {
    background: rgba(208, 71, 209, 0.5);
}

.social-vk {
    background: rgba(139,157,195, 0.2);
}

.social-vk:hover {
    background: rgba(139,157,195, 0.5);
}

.logo {}

.logo-container {
    display: flex;
    justify-content: center;
    margin-bottom: 50px;
    margin-top: 6vh;
}
/*_____MAIN____*/
.menu {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-bottom: 50px;
}

.menu-category {
    position: relative;
    display: flex;
    overflow: hidden;
    margin-bottom: 30px;
    max-width: 240px;
    font-family: 'Roboto', sans-serif;
    font-size: 16px;
    color: #ffffff;

    img {
        width: 100%;
        height: 100%;
    }
}

.menu-category * {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    -webkit-transition: all 0.25s ease;
    transition: all 0.25s ease;
}

.menu-category:before {
    position: absolute;
    top: 10px;
    bottom: 10px;
    left: 10px;
    right: 10px;
    top: 100%;
    content: '';
    background-color: rgba(51, 51, 51, 0.7);
    -webkit-transition: all 0.25s ease;
    transition: all 0.25s ease;
    -webkit-transition-delay: 0.25s;
    transition-delay: 0.25s;
}

.menu-category img {
    width: 100%;
    backface-visibility: hidden;
}

.menu-category figcaption {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 1;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
}

.menu-category h3,
.menu-category h5 {
    opacity: 0;
    letter-spacing: 1px;
}

.menu-category h3 {
    max-width: 90%;
    line-height: 20px;
    margin-bottom: 5px;
    text-align: center;
    -webkit-transform: translateY(-100%);
    transform: translateY(-100%);
    text-transform: uppercase;
    font-weight: 400;
    -webkit-transition-delay: 0.05s;
    transition-delay: 0.05s;
}

.menu-category h5 {
    font-weight: normal;
    background-color: $crocus;
    padding: 5px 10px;
    -webkit-transform: translateY(-100%);
    transform: translateY(-100%);
    -webkit-transition-delay: 0s;
    transition-delay: 0s;
}

.menu-category:hover:before {
    top: 10px;
    -webkit-transition-delay: 0s;
    transition-delay: 0s;
}

.menu-category:hover h3,
.menu-category:hover h5 {
    -webkit-transform: translateY(0);
    transform: translateY(0);
    opacity: 1;
    margin-bottom: 10px;
}

.menu-category:hover h3 {
    -webkit-transition-delay: 0.3s;
    transition-delay: 0.3s;
}

.menu-category:hover h5 {
    -webkit-transition-delay: 0.2s;
    transition-delay: 0.2s;
}
/*_____FOOTER____*/
.footer {
    margin-top: 10vh;
    background: rgba(51, 0 , 51, 0.1);
}

.footer-inner {
    display: flex;
    flex-direction: column;
    max-width: 1200px;
    margin: 0 auto;
    padding: 5vh;
}

.footer-bar {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-around;
    border-bottom: 1px solid #999;
    padding-bottom: 3vh;
}

.footer-about {
    width: 240px;
    text-align: center;

    p {
        margin-top: 3vh;
        line-height: 20px;
        color: #999999;
    }
}

.footer-ava {
    height: 240px;
    transition: all 0.5s ease-in-out;

    &:hover {
        transform: rotate(36deg) scale(1.1);
    }
}

.footer-about,
.footer-blog,
.footer-menu {
    margin-bottom: 2vh;
}

.footer-menu {
    color: white;
    text-align: center;
    letter-spacing: 2px;

    li {
        margin-bottom: 10px;
    }
}

.footer-menu-title {
    display: block;
    margin-bottom: 20px;
    font-size: 14px;
    font-weight: bold;
}

.footer-menu-item {
    color: #999999;

    &:hover {
        color: $crocus;
    }
}

.footer-blog {
    max-width: 480px;
    padding-left: 20px;
    padding-right: 20px;
    text-align: center;
    line-height: 20px;
    letter-spacing: 1px;
    color: #fff;
}

.footer-contacts {
    padding-top: 5vh;
    text-align: center;
    color: #999999;
    line-height: 30px;

    a {
        color: white;

        &:hover {
            color: $crocus;
        }
    }
}

.footer-social {
    a {
        margin-right: 20px;
        font-size: 12px;
        color: white;
        text-transform: uppercase;
    }
}
@import "media";
